.tags-wrapper {
	position: relative;
	overflow: hidden;
	z-index: 999;
	height: auto;
	margin-top: 5px;

	.icons-tag {
		position: absolute;
		top: 0;
		left: 0;
		font-size: 16px;
		line-height: 1;
	}

	.toggle-tags {
		$margin-left: 8px;
		height: auto;
		padding-left: $grid-padding + $margin-left;
		@include vendor-prefix(transform, translate3d(0,0,0));
		@include vendor-prefix(transition, 0.2s max-height ease-in-out);

		&.tags-contracted {
			width: 100%;
			max-height: 23px;
			overflow: hidden;
		}

		li {
			display: block;
			float: left;
			margin-right: 4px;
			margin-bottom: 4px;

			a {
				display: block;
			}
		}
	}
}
