.table-1 {
	tbody {
		tr {
			td {

				.row-subtitle {
					padding: 1em 0;
				}

				.row-subtitle,
				.row-description {
					color: $table-1-row-description;
				}

				a {
					&:hover {
						.row-description {
							color: $link-color;
						}
					}
				}
			}
		}
	}

	&.table-single-column {
		@include respond-to("medium") {
		 //margin-top: 5px;
		}
		th, td {
			&:first-child {
				padding-left: 20px;
			}
			&:last-child {
				padding-right: 20px;
			}
		}
	}
}
