.info-boxes-wrapper {
	display: table;
	border-spacing: 4px;
	border-collapse: separate;
	margin-left: 16px;
	/* margin-right: 16px; */

	[class*="col-"] {
		float: none;
		display: table-cell;
		border: 1px solid $darkbg-primary-text;
	}

	@include respond-to("medium") {
		display: block;
		margin-right: 16px;

		[class*="col-"] {
			display: block;
		}

		[class*="col-"] + [class*="col-"] {
			margin-top: 20px;
		}
	}

	.info-box {
		.top-label {
			position: relative;
			min-height: 60px;

			.top-label-title {
				position: relative;
				padding: 15px 0 10px;
				z-index: 2;
				background-color: #6b758f;

				p {
					margin-bottom: 0;
				}
			}
		}
		.top-label-bg {
			position: relative;
			width: 100%;
			z-index: 1;

			img {
				width: 100%;
				height: 24px;
			}
		}

		.info-box-content {
			padding: 20px;
		}
		.top-label, .top-label + .info-box-content {
			font-size: 13px;
			line-height: 1.4;
		}
	}
}
