
.heading-labeled {
	margin-bottom: 20px;
	.label {
		display: block;
		float: left;

		@include respond-to("medium") {
			height: 45px;
			padding: 7px;
			line-height: 30px;
			font-size: 8px;
		}
	}
	.heading-text-w {
		float: left;
		padding: 0 20px;

		@include respond-to("medium") {
			padding-left: 8px !important;
		}

		p {
			font-size: 18px;
			margin-bottom: 7px;
		}
		h1 {
			margin-top: 0;
		}

		@include respond-to("medium") {
			p {
				font-size: 13px;
				line-height: 18px;
			}

			h1 {
				font-size: 18px;
				line-height: 22px;
			}
		}


		@include respond-to("medium") {
			padding: 0;

			h1 {
				padding: 0;
			}
		}
	}
}

.detached {
	.heading-labeled {
		padding: 15px !important;
		margin-bottom: 0;
		padding-bottom: 0 !important;

		@include respond-to("medium") {
			padding-bottom: 15px !important;
		}

		.heading-labeled-jail {
			width: 65%;

			@include respond-to("medium") {
				width: 100%;
			}
		}

		.label {
			padding: 7px;
			font-size: 11px;
		}

		.heading-text-w {
			padding: 0 15px;
			margin-left: 0 !important;

			p {
				color: #3a4855;
				font-size: 13px;
				line-height: 18px;
				margin-bottom: 0;
			}

			h1 {
				font-size: 18px;
				line-height: 22px;

				@include respond-to("medium") {
					font-size: 14px;
					line-height: 18px;
				}
			}
		}
	}
}

.sticky-header-wrapper {
	position: relative;
	min-height: 1px;
	z-index: 998;
	overflow:hidden;
	backface-visibility: hidden;


	&.detached {
		position: fixed;
		top: -999px;
		background-color: $body-bgc;
		padding: 0;
		z-index: $z-index-10-sticky-header-detached;

		.header-wrap {
			background-color: $darkbg-primary-text;
		}

		.sticky-header {
			min-height: 30px;
			background-color: $darkbg-primary-text;
			@include respond-to("medium") {
				display: none;
			}

			.sticky-header-values {
				padding-top: 0;
				padding-bottom: 0;
				padding-left: 70px;

				@include respond-to("large") {
					padding-bottom: 10px;
				}

				.col-xl-4 {
					display: none;

					&.detached-visible {
						display: block;
						width: 100%;

						p {
							display: inline;
							font-size: 13px;

							&.h2 {
								font-weight: bold !important;
							}

							small {
								font-size: 11px;
							}
						}

						.hr {
							display: none;
						}
					}
				}
			}

			.sticky-header-downloads-wrapper {
				margin-top: -53px;
			}
		}

		.space-2 {
			height: 4px;
			max-height: 4px;
			margin: 0;
			background-color: #707a87;
		}

		.scrollable-tabs {
			z-index: 999;
		}

		.tabs-wrapper {
			background-color: $body-bgc;
			padding: 0 10px 10px 10px;
		}
	}
}

@include ie8 {
	.sticky-header-wrapper.detached {
		.scrollable-tabs.scrollable-tabs-arrowed {
			height: 45px;
		}
	}
}

.sticky-header {
	min-height: 85px;
	background-color: $darkbg-primary-text;
	> [class*="col-"] {
		padding: 20px 10px;
		.row > [class*="col-"] {
			padding: 0 5px;
		}
	}

	.sticky-header-values {
		@include respond-to("medium") {
			padding: 10px;
			.row {
				.col-md-6:nth-child(2) {
					margin-left: 0 !important;
				}
			}
		}
	}

	p {
		margin-bottom: 0;
		font-size: 13px;

		&.h2 {
			font-weight: normal !important;
			font-family: $basic-font;
			font-size: 20px;

			@include respond-to("medium") {
				font-size: 13px;
			}
		}

		small {
			font-size: 11px;
		}
	}
	.hr {
		background-color: #adb5bd;
		background-color: rgba(173, 181, 189, 0.5);
	}
	.sticky-header-downloads-wrapper {
		@include respond-to("medium") {
			padding: 0 10px 10px;

			.hr {
				min-height: 1px;
				background-color: #FFF;
				background-color: rgba(255, 255, 255, 0.5);
				margin-bottom: 10px;
			}
		}
	}
}

.sticky-header-visible {
	.progress-bar {
		top: auto;
		bottom: 11px;
	}
}
