.document-center-tiles {
	.dct-wrapper {
		display: block;
		position: relative;
		height: 360px;
		overflow: hidden;

		@include respond-to("medium") {
			padding-bottom: 0 !important;
			margin-bottom: 4px !important;

			&:last-child {
				margin-bottom: 0;
			}
		}

		&:hover {
			background-color: $contentdarkbg;
		}

		.dct-link {
			position: relative;
			display: block;
			height: 100%;
			text-decoration: none;

			&:hover,
			&:focus {
				text-decoration: none;
			}

			@include respond-to("medium") {
				min-height: 0;
			}
		}

		.bg-image {
			z-index: 1;
			width: auto;

			@include respond-to("medium") {
				width: 100%;
			}
		}

		.tags {
			position: absolute;
			top: 20px;
			left: 20px;
			z-index: 2;

			@include respond-to("medium") {
				position: relative;
				padding-bottom: 20px;
			}
		}

		.dct-overlay {
			position: relative;
			padding: 20px;
			width: 100%;
			height: 210px;
			z-index: 2;

			header {
				padding-bottom: 8px;
			}

			p {
				color: $base;
			}

			.dct-main {
				height: 90px;
			}

			.dct-footer {
				width: 100%;
				height: 80px;

				.dct-tags {
					padding-bottom: 8px;
					margin-bottom: 15px;
					border-bottom: 1px solid #FFFFFF; // fix for ie8
					border-bottom: 1px solid rgba(#FFFFFF, 0.2);
					color: white;
					font-size: 11px;
					line-height: 15px;
				}
			}
		}
	}

	.dct-type-1 {
		.dct-overlay {
			position: absolute;
			left: 0;
			bottom: 0;
			background-color: rgba(#98a2ac, 0.85);
			filter:progid:DXImageTransform.Microsoft.Gradient(GradientType=0.85, StartColorStr='#8598a2ad', EndColorStr='#8598a2ad');
		}

		.dct-footer {
			z-index: 3;
		}
	}

	.dct-type-2 {
		.tags {
			position: relative;
			@include respond-to("medium") {
				padding-bottom: 0;
			}
			.date {
				border: 1px solid #b4bcc4;
				padding: 0 5px 1px;
			}
		}
		.dct-overlay {
			height: 321px;
			.dct-main {
				height: auto;
			}

			.dct-footer {
				position: absolute;
				bottom: 0;
				left: 0;
				height: 101px;
				padding: 0 20px 20px;
			}
		}
	}
}
