$article-navegation-item-hover: $amber !default;
$article-navegation-border-color: $white !default;
$article-navegation-border-color-active: $amber !default;

.article-navegation {
	$arrow-margin-left: 10px;
	$arrow-margin-right: 30px;

	.icons-arrow-down {
		font-size: 10px;
		margin-left: 9px;
	}


	//
	// Geral
	//
	&__link {
		text-overflow: ellipsis;
		white-space: nowrap;
		overflow: hidden;
	}

	//
	// @overwrite behavior
	//
	.tabs .active {
		border-color: $article-navegation-border-color-active;
	}
}


//
// Element related
//
.article-navegation__related {
	height: 41px;

	.tabs li a {
		padding-bottom: 16px;
		border-bottom: 1px solid $article-navegation-border-color;
		padding-right: 32px;
		height: 16px;

		&:hover {
			padding-bottom: 12px;
			border-color: $article-navegation-border-color-active;
		}
	}

	.tabs {
		li a:hover,
		.active a,
		.active:hover a {
			padding-bottom: 12px;
			border-bottom: 5px solid $article-navegation-border-color-active;
		}
	}

	.article-navegation__link {
		max-width: 239px;
	}
}


//
// Element links
//
.article-navegation__links {
	height: 35px;
	.tabs  li a {
		padding-right: 53px;
	}

	.tabs li a {
		font-size: 14px;
		border: none;
		padding-bottom: 3px;
	}

	.tabs li {
		min-height: 28px;
	}

	.tabs-wrapper .tabs li {
		min-height: auto;
	}

	.tabs li:hover a {
		color: $article-navegation-item-hover;
		border: none;
	}

	.article-navegation__link {
		max-width: 193px;
		position: relative;

		i {
			position: absolute;
			right: 33px;
		}
	}

	.active a {
		color: $article-navegation-item-hover
	}

	//
	// Inner components
	//
	.progress-bar {
		top: auto;
		bottom: 0;
	}
}

//
// Mobile navigation
//
.article-navigation-mobile {
	display: none; // activation by media-queries
	position: absolute;
	right: 5px; left: 5px;
	z-index: 10000;
	background-color:$blue-gray;
	color: white;
	border: 1px solid;
	overflow: hidden;
	max-height: 32px; 
	
	.article-navigation-mobile__item {
		color: inherit;
		padding: 5px 10px; 
		opacity: 0;
		visibility: hidden;
		z-index: 10;
		position: relative;
		display: block;
		transition: all .3s linear; 
		&:hover {
			text-decoration: none;
			background-color: $article-navegation-item-hover;
		}
	}

	&.active {
		max-height: 100%;
		.article-navigation-mobile__item {
			opacity: 1;
			visibility: visible;
		}
	}

	.active.article-navigation-mobile__item {
		opacity: 1;
		visibility: visible;
		&:hover {
			background-color: inherit;
		}
	}

	.icons-arrow-down {
		position: absolute;
		top: 0; right: 0;
		padding: 5px 10px;
	}
}