.modal-content {
    border-radius:0;
    .h1 {
    	@extend .text-highlighted;
    }
    .page-hl, .h1 {
		padding-left:0;
	}
}
.bootbox-body {
	padding-left: 15px;

}
.bootbox-close-button {
	@extend .icons-close-badge;
	position:absolute;
	top:0;
	right:0;
	border:0;
	margin:0 !important;
}
.btn.btn-default,
.btn.btn-primary {
	@extend .button;
	border:0;
}
.modal-footer {
	border:0;
	padding: 0 30px 20px
}
.content--lb {
	padding-right: 30px;
	p {
		margin-bottom:12px
	}
	h2, .h2 {
		color: $darkbg-primary-text;
		margin-bottom: 0
	}

}
.content--lb.fixed-height {
	max-height:260px;
	overflow: auto;
}
.bootbox-body [class*="col-"] {
    margin-left:0;
}
.modal-backdrop.in {
	opacity: 0.8;
}
.modal_video {
	.modal-dialog {
		width:630px;
		@include respond-to("medium") {
			width:auto;
		}
	}
	
	.content--lb {
		position: relative;
		padding-bottom:30px;
		padding-right:15px;
	}
}

.loader-wrapper {
	display: table;
	position: absolute;
	width: 100%;
	height: 250px;
	z-index: 1000;

	@include respond-to('medium') {
		height: 100px;
	}

	.img-wrapper {
		display: table-cell;
		vertical-align: middle;
		
		img {
			margin: 0 auto;
			width: 64px;
		}
	}

}
/* IE 7 - 8 */
@include ie8 {
	.modal-dialog {
	    width: 570px;
	    margin: 30px auto;
  	}
}
/* IE 7 - 9 */
@include ie9 {
	.modal-scrollbar-measure {
		display:none
	} 
}