.sidebar {
	li {
		padding: 15px 0 12px;
		border-bottom: 1px solid $hr-content;

		a {
			display: block;
			overflow: hidden;
			color: $text-primary;
			span {
				font-size: 18px;
			}
			& > span {
				float: left;
				width: 80%;
			}
		}

		&:first-child {
			padding-top: 0;
		}
	}

	i {
		color: $link-color;
	}

	.lk-icon {
		i {
			float: left;
			min-width: 25px;
		}
	}

	.icons-location {
		font-size: 24px;
  		vertical-align: middle;
	}
	.icons-play,
	.icons-globe-filled {
		font-size: 18px;
	}
}
