.download-box {
	&:nth-child(even) {
		.internal-concept {
			background-color: #e6e9eb;
		}
	}

	.tags {
		margin-bottom: 10px;
		@include respond-to('medium') {
			margin-bottom: 5px;
		}
		.tag {
			margin-top: 2px;
			font-weight: bold;

			@include respond-to('medium') {
				margin-top: 0;
			}

			&:hover {
				color: $link-color;
				text-decoration: none;
			}
		}
	}

	.emphasize {
		color: $text-primary;

		&:hover {
			text-decoration: none;
		}
	}

	.download-link {
		font-size: 13px;
		line-height: 40px;
		color: $text-primary;

		@include respond-to("small") {
			padding-left: 15px;
		}

		&:hover {
			color: $link-color;
			text-decoration: none;
		}

		.icons-download-small {
			top: 12px;
		}
	}

	div.download-link, div.download-link:hover {
		color: #adb0b3;

		.icons-download-small {
			visibility: hidden;
		}
	}

	.col-lg-6 {
		&:nth-child(2n+1) {
			margin-left: 0;
		}
	}
}
