@if $components-version == 'onlinebanking' {
	.icons-internal-link {
		@extend .icons-arrow-right;
		background-image: none !important;
		background-color: $icons-more-link-bgc;
		text-indent: 0 !important;
		position: relative;
		top: 6px;

		&:before,
		&:after {
			width: 100%;
			height: 100%;
			font-size: 7px !important;
			color: $white;
			vertical-align: top;
			line-height: 1.4 !important;
			text-align: center;

			&:hover,
			&:focus {
				color: $white;
			}
		}
	}

	.icons-more-link {
		position: relative;
		width: $icons-more-link_width;
		height: $icons-more-link_height;
		background-color: $icons-more-link-bgc;
		color: $icons-more-link-color;
		background-image: none !important;
		text-indent: 0 !important;
		@extend .icons-arrow-down;

		&:before,
		&:after {
			position: absolute;
			font-size: 6px;
			left: 2px;
			top: 2px;
		}

		// @ie-8
		.lt-ie8 & {
			@include sprite($icons-more-link);
		}
	 }
}
