.pricing-chart-wrapper {
	height: 300px;
}

.performance-chart {
	$padding: 10px;
	.filter-tags {
		.tag {
			margin: 0 4px 4px 0;
			font-size: 12px;

			a {
				padding: 0 8px;

				span {
					font-size: 1em;
					line-height: 32px;
				}
			}
		}
	}

	$size: 32px;
	.datepicker-wrapper {
		position: relative;
		height: $size;
		float: left;

		input {
			width: 100%;
			height: $size;
			margin: 0;
			border: 1px solid #91979d;
		}

		.ui-datepicker-trigger {
			position: absolute;
			top: 0;
			right: 0;
			width: $size;
			height: $size;
			padding: 0 8px;
			border: 0;
			font-size: 0;
			background-color: #3d4855;
		}
	}

	.datepicker-arrow {
		text-align: center;
		line-height: $size;
	}

	.datepicker-action {
		text-align: right;
		line-height: $size;
		margin-bottom: 0;
		padding-left: 10px;
	}
	.highcharts-tooltip {
		> span {
			padding: 20px;

			.stock-chart-tooltip {
				border-collapse: separate;
    			border-spacing: 8px;

    			th, td {
    				padding-top: 0;
    				padding-left: 0;
    			}

				thead {
					th {
						font: normal 13px/18px $basic-font;
						color: #3d4855;
					}
				}

				tbody {
					th {
						font: normal 13px/18px $basic-font;
						color: #98a2ad;
						border-bottom: 1px solid #d8dde1;
						padding-bottom: 2px;
					}

					td {
						font: normal 20px/20px $basic-font;
						color: #4f5356;
						vertical-align: top;
						height: 30px;
					}
				}
			}
		}
	}
}

// DATEPICKER

// remove corners
#ui-datepicker-div,
.ui-datepicker-header {
	@include vendor-prefix(border-radius, 0);
}

.ui-datepicker-header {
	background: #333333;
}

.ui-state-default {
	background: #555555 !important;
}

.ui-state-active {
	background: #f58400 !important;
}

@include ie8 {
	.datepicker-wrapper {
		input {
			width: 97%;
			height: 29px;
			line-height: 29px;
		}
	}
}


.geographic-exposures {
	position: relative;

	.geographic-exposures-map {
		position: absolute;
		top: 0;
		left: 0;
		img {
			width: 100%;
		}
	}

	$w: 513;
	$h: 300;
	.geographic-exposures-data {
		position: relative;
		top: 0;
		left: 0;
		width: $w * 1px;
		height: $h * 1px;
		-webkit-transform-origin: 0 0;
		-moz-transform-origin: 0 0;
		-ms-transform-origin: 0 0;
		transform-origin: 0 0;

		span {
			position: absolute;
			display: none;
			width: 76px;
			height: auto;
			color: white;
			min-height: 60px;
			padding: 7px 9px;
			font-size: 13px;
			line-height: 18px;

			&.arrow-bottom {
				&:after {
					top: 100%;
					left: 50%;
					border: solid transparent;
					content: " ";
					height: 0;
					width: 0;
					position: absolute;
					pointer-events: none;
					border-color: transparent;
					border-width: 6px;
					margin-left: -6px;
				}
			}

			&.arrow-right {
				&:after {
					left: 100%;
					top: 50%;
					border: solid transparent;
					content: " ";
					height: 0;
					width: 0;
					position: absolute;
					pointer-events: none;
					border-color: transparent;
					border-width: 6px;
					margin-top: -6px;
				}
			}

			&.arrow-top {
				&:after {
					bottom: 100%;
					left: 50%;
					border: solid transparent;
					content: " ";
					height: 0;
					width: 0;
					position: absolute;
					pointer-events: none;
					border-color: transparent;
					border-width: 6px;
					margin-left: -6px;
				}
			}

			&.north-america {
				@include get-map-positioning(73, 6, $w, $h);
			}

			&.latin-america {
				@include get-map-positioning(73, 208, $w, $h);
			}

			&.europe {
				@include get-map-positioning(230, 6, $w, $h);
			}

			&.africa {
				@include get-map-positioning(230, 208, $w, $h);
			}

			&.asia {
				@include get-map-positioning(365, 85, $w, $h);
			}
		}
	}
}
