.notification {
	padding-left: 50px;
	min-height: 55px;
	padding-top: 10px;
	display: block;
	position: relative;

	i {
		position: absolute;
		left: 0;
		top: 0;
		font-size: 40px;
	}

	&--success {
		color: $notification-success;
	}

	&--warning {
		color: $notification-warning;
	}

	&--error {
		color:  $notification-error;
	}
}
