.tiles {
	.tile {
		margin-bottom: 4px;

		&:nth-child(3n+1) {
			margin-left: 0;
		}

		@include respond-to("large") {
			&:nth-child(3n+1) {
				margin-left: gutter();
			}

			&:nth-child(odd) {
				margin-left: 0;
			}
		}
		
		@include respond-to("large") {
			.m-block__figure img {
				position: static;
			}
		}
	}
}

@include ie8 {
	.tile[data-position="1"] {
		margin-left: 0;
	}
}
