.subsnews {
	padding: 48px 0;
	background-color: $subsnews-bgc;
	color: $subsnews-color;

	// @note @note-selector replace it by another selector
	& > a {
		text-decoration: none;
	}

	&__headline {
		font-weight: bold;
		font-size: 14px;
		font-family: $basic-font;
		color: $subsnews-headline-color;
	}

	&__link {
		color: $subsnews-link-color;
		text-decoration: underline;
	}

	&__form {
		border-right: 1px solid #4c5765;
	}

	&__description, &__flash {
		position: relative;

		i {
			position: absolute;
			left: 0;
			top: 0;
		}
	}

	&__description {
		padding-left: 65px;
		color: $subsnews-color;
		i {
			font-size: 56px;
		}
	}

	.icons-feed {
		color: $subsnews-icon-color;
	}

	.form__option {
		padding-left: 20px;
		position: relative;

		input[type="checkbox"] {
			position: absolute;
			left: 0;
			top: 5px;
		}
	}
}
