.lg-outer {
    .lg-thumb-outer {
        bottom: 0;
        position: absolute;
        max-width: 980px;
        width: 100%;
        margin: 0 auto;
        padding: 0 6px;
        z-index: $zindex-thumbnail;
        max-height: 350px;
        background: black;
        @include translate3d(0, 100%, 0);
        @include transitionCustom(transform 0.25s cubic-bezier(0, 0, 0.25, 1) 0s);

        &.lg-grab {
            .lg-thumb-item {
                @include grab-cursor;
            }
        }

        &.lg-grabbing {
            .lg-thumb-item {
                @include grabbing-cursor;
            }
        }

        &.lg-dragging {
            .lg-thumb {
                @include transition-duration(0s !important);
            }
        }

        @include respond-to('medium') {
        	display: none;
        }
    }
    &.lg-thumb-open{
        .lg-thumb-outer {
            @include translate3d(0, 0%, 0);
        }
    }

    .lg-thumb {
        padding: 10px 0;
        height: 100%;
        margin-bottom: -5px;
    }

    .lg-thumb-item {
        cursor: pointer;
        float: left;
        overflow: hidden;
        height: 100%;
        margin-bottom: 5px;

        img {
            width: 100%;
            height: 100%;
        }
    }

    &.lg-has-thumb {
        .lg-item {
            padding-bottom: 120px;
        }
    }

    &.lg-can-toggle {
        .lg-item {
            padding-bottom: 0;
        }
    }
    &.lg-pull-caption-up{
        .lg-sub-html {
            @include transition(bottom 0.25s ease);
        }
    }

    .lg-toogle-thumb {
        color: $amber;
        cursor: pointer;
        font-size: 20px;
        height: 39px;
        line-height: 27px;
        padding: 5px 0;
        position: absolute;
        right: 20px;
        text-align: center;
        top: -35px;
        width: 50px;
        background: black;

        &:after {
            @include icons-tile;
        }

        &:hover {
            color: lighten($amber, 10%);
        }

        @include respond-to('medium') {
        	display: none;
        }
    }
}

.lg-wrapper-fix {
	overflow: hidden;
	height: 100%;
}
