// default theme
.lg-actions {
    .lg-next, .lg-prev {
        color: $lg-next-prev-color;
        cursor: pointer;
        display: block;
        font-size: 22px;
        padding: 8px 10px 9px;
        position: absolute;
        top: 47%;
        z-index: $zindex-controls;

        @include respond-to('medium') {
        	margin-top: -10px;
        }

        &.disabled {
            pointer-events: none;
            opacity: 0.5;
        }

        &:hover {
            color: $lg-next-prev-hover-color;
        }
    }

    .lg-next {
        right: -60px;

        @include respond-to('xlarge') {
        	right: 20px;
        }

        &:before {
            @include icons-arrow-right;
            font-size: 33px;
            color: $amber;
        }
    }

    .lg-prev {
        left: -60px;

		@include respond-to('xlarge') {
			left: 20px;
		}

        &:after {
            @include icons-arrow-left;
            font-size: 33px;
            color: $amber;
        }
    }
}

@include keyframes(lg-right-end) {
    0% {
        left: 0;
    }

    50% {
        left: -30px;
    }

    100% {
        left: 0;
    }
}


@include keyframes(lg-left-end) {
    0% {
        left: 0;
    }

    50% {
        left: 30px;
    }

    100% {
        left: 0;
    }
}


.lg-outer {
    &.lg-right-end {
        .lg-object {
            @include animation(lg-right-end 0.3s);
            position: relative;
        }
    }

    &.lg-left-end {
        .lg-object {
            @include animation(lg-left-end 0.3s);
            position: relative;
        }
    }
}

// lg toolbar
.lg-toolbar {
    z-index: $zindex-toolbar;
    left: 0;
    position: absolute;
    top: 20px;
    width: 100%;

    @include respond-to('large') {
    	top: 0;
    }

    .lg-icon {
        color: $lg-toolbar-icon-color;
        cursor: pointer;
        float: right;
        font-size: 24px;
        height: 47px;
        line-height: 27px;
        padding: 10px 0;
        text-align: center;
        width: 50px;
        text-decoration: none !important;
        outline: medium none;

        &:hover {
            color: $lg-toolbar-icon-hover-color;
        }
    }

    .lg-close {
        &:after {
           	@include icons-close;
           	color: $amber;
           	font-size: 16px;
        }
    }

    .lg-download {
    	display: none;
        &:after {
            @include icons-download;
        }
    }
}

// lightGallery title
.lg-sub-html {
    background-color: $lg-sub-html-bg;
    color: $lg-sub-html-color;
    font-size: 16px;
    left: 0;
    padding: 30px 45px 10px 0;
    position: absolute;
    right: 0;
    z-index: $zindex-subhtml;

    h2 {
        color: white;
    }

    p {
    	color: white;
        font-size: 18px;
        font-family: $main-font;
        margin: 5px 0 0;

        @include respond-to('medium') {
        	display: none;
        }
    }

    @include respond-to('xlarge') {
    	padding: 10px 30px;
    	top: 0;
    }

    @include respond-to('medium') {
    	padding-top: 10px !important;
    }
}

// lg image counter
#lg-counter {
    color: $lg-icon-color;
    display: inline-block;
    font-size: $lg-counter-font-size;
    padding-left: 20px;
    padding-top: 12px;
    vertical-align: middle;
    display: none;
}

