$social-bar-btn-width: 26px;
$social-bar-btn-height: 25px;



.active {
	.social-bar__btn {
		background-color: $social-bar-mobile-bgc;
		opacity: 1 !important;
		@include vendor-prefix-transition(background-color 0 0 linear);
		@include vendor-prefix-transition(transform 0.4s cubic-bezier(0, 0, 0.58, 1));
	}

	.social-bar__btn .icons-share-thin {
		color: $breadcrumb-options-share-icon-color-active-color;
	}

	.social-bar {
		@include vendor-prefix(transform, translateX(0));
		@include vendor-prefix-transition(transition all 0.4s cubic-bezier(0.42, 0, 0.58, 1));
	}

	// States
	.social-bar__btn:hover {
		color: red;
	}

}

.social-bar {
	&__wrapper {
		display: block;
		overflow: hidden;
		height: $social-bar-btn-height;
	}
}

.social-bar__btn {
	$icon-size: 16px;
	font-size: $icon-size;
	line-height: $icon-size;
	width: $social-bar-btn-width;
	height: 100%;
	display: block;
	float: left;
	position: relative;
	z-index: 2;
	transition-delay: 0.8s;
	color: $white;
	background-color: transparent;
	border: none;
	@include vendor-prefix-transition(background-color 0.1s 300ms cubic-bezier(0.42, 0, 0.58, 1));

	i {
		position: relative;
		top: 2px;
		left: -2px;
	}

	&.social-bar__btn--grey {
		i {
			color: $breadcrumb-options-share-icon-color-grey;
		}
	}

	&:focus {
		outline: none;
		border: none;
	}

	@include respond-to("up-than-large") {
		&:hover {
			color: $breadcrumb-options-share-icon-color-active;
		}
	}

	@include respond-to("medium") {
		opacity: 0.5;
		&:hover {
			opacity: 1;
		}
	}

	@include respond-to("large") {
		padding: 2px 5px 4px;
	}
}


.social-bar {
	background-color: $white;
	padding: 0 10px 0;
	float: left;
	height: 100%;
	z-index: 1;
	@include vendor-prefix(transform, translateX(200%));
	@include vendor-prefix-transition(all 0.4s cubic-bezier(0.42, 0, 0.58, 1));
}


aside {
	.social-bar__text {
		float: left;
	}

	.social-bar__wrapper {
		height: auto;
	}

	.social-bar {
		height: 32px !important;
		right: 32px;
		width: 270px;
		position: absolute;
		padding-top: 5px;
		padding-bottom: 3px;
		height: auto;
		@include vendor-prefix(box-sizing, border-box);
	}

	.social-bar__btn {
		width: auto;
		padding: 9px 7px 4px;
		background: $social-bar-mobile-bgc;
		transition: opacity 300ms 300ms linear;
		opacity: 0.5;

		&:hover {
			color: $white;
		}

		i {
			top: -1px;
			left: 0;
		}
	}

	.active .social-bar__btn {
		opacity: 1;
	}
}

.social-bar__wrapper + .sticker-toggle {
    top: 35px;
}

.social-bar__text {
	display: inline-block;
	vertical-align: middle;
	font-size: 11px;
	color: $gray;
	line-height: 25px;
}

@include ie9 {
	.social-bar__text {
		width: auto !important
	}

	.social-bar__btn i {
		left: -4px;
		top: 2px;
	}
}


//
// shariff plugin customization
//
.shariff {
	display: inline-block;
	line-height: 25px;

	.orientation-horizontal {
		background-color: transparentize;
		margin: 0 !important;
		padding: 0 0 0 5px;
	}

	.shariff-button {
		display: inline-block;
		list-style: none;
		min-width: 20px;
		text-align: center;
		margin: 0 4px;
		vertical-align: middle;
		padding: 0;

		&:first-child {
			padding: 0;
		}

		.share_text {
			display: none;
		}

		a {
			font-size: 16px;
			vertical-align: middle;
			padding: 0;
			-webkit-backface-visibility: hidden;

			&:hover {
				text-decoration: none;
			}
		}
	}


	.fa-facebook:before {
		@include icons-fb;
	}

	.fa-twitter:before {
		@include icons-tt;
	}

	.fa-google-plus:before {
		@include icons-gp;
		font-weight: bold;
	}

	.fa-linkedin {
		margin: 0;
		&:before {
			@include icons-lk;
		}
	}

	.fa-xing:before {
		@include icons-xg;
	}

	.fa-envelope:before {
		@include icons-mail-filled;
	}

	.facebook {
		min-width: 0;
	}

	.facebook a {
	    color: $facebook-color;
	    &:hover {
	    	color: rgba($facebook-color, 0.8);
	    }
	}

	.twitter a {
	    color: $twitter-color;

	    &:hover {
	    	color: rgba($twitter-color, 0.8);
	    }
	}

	.googleplus a {
	    color: $google-plus-color;
	    &:hover {
	    	color: rgba($google-plus-color, 0.8);
	    }
	}

	.linkedin a {
	    color: $linkedin-color;
	    &:hover {
	    	color: rgba($linkedin-color, 0.8);
	    }
	}

	.xing a {
	    color: $xing-color;
	    &:hover {
	    	color: rgba($xing-color, .8);
	    }
	}

	.mail a {
	    color: $amber;
	    &:hover {
	    	color: $amber-2;
	    }
	}

	.shariff-button:hover > a {
		color: $breadcrumb-options-share-icon-color-active-item-hover;
	}

	/* .mail:hover > a {
		color: #e8a945;
	} */
}

//
// Browser specific & Fallback
//
.lt-ie9 {
	.social-bar__wrapper {
		.shariff-button a {
			display: block;
			width: 20px;
			height: 25px;
			overflow: hidden;
			position:relative;
		}
	}


	.facebook a:before {
		@include icons-fb;
	}

	.twitter a:before {
		@include icons-tt;
	}

	.googleplus a:before {
		@include icons-gp;
		font-weight: bold;
	}

	.linkedin {
		margin: 0;
		a:before {
			@include icons-lk;
		}
	}

	.xing a:before {
		@include icons-xg;
	}

	.mail a:before {
		@include icons-mail-filled;
	}

	.facebook {
		min-width: 0;
	}

	.facebook a:before {
	    color: $facebook-color;
	}

	.twitter a:before {
	    color: $twitter-color;
	}

	.googleplus a:before {
	    color: $google-plus-color;
	}

	.linkedin a:before {
	    color: $linkedin-color;
	}

	.xing a:before {
	    color: $xing-color;
	}

	.mail a:before {
	    color: $mail-color;
	}

	.shariff-button a:hover:before {
		opacity: 0.8;
	}
}


.lt-ie10 {
	.social-bar__btn {
		padding-top: 3px;
		padding-bottom: 3px;
	}

	.social-bar__wrapper .social-bar {
		display: none;
	}

	.social-bar__wrapper.active .social-bar {
		display: inline-block
	}
}


.no-flexbox {
	.social-bar__wrapper {
		overflow: visible;
		position: relative;
	}

	.social-bar--left {
		.social-bar {
			width: 265px !important;
		}
	}

	.social-bar__btn {
		padding-left:8px;
		padding-right:8px;
	}

	.social-bar {
		position: absolute;
	    right: 0;
	    width: 280px;
	    opacity: 0;
	    display: none;
	}

	// States
	.active .social-bar {
		right: 0;
		opacity: 1;
		display: inline-block;
	}
}

.social-bar__wrapper.social-bar--left {
	.social-bar {
		-webkit-transform: translateX(-200%);
		transform: translateX(-200%);
		position: relative;
	}

	&.active {
		.social-bar__btn {
			i {
				color: $white;
			}
		}

		.social-bar {
			-webkit-transform: translateX(0);
			transform: translateX(0);
		}
	}
}

.social-bar__wrapper.social-bar--teaser {
	.social-bar__btn {
		width: 35px;
	}

	.social-bar {
		width: 235px;
		height: 32px;

		span, .shariff {
			vertical-align: middle;
			margin-top: 3px;
		}
	}
}

.social-bar__wrapper.social-bar--no-margin{

	@include respond-to("large") {
		.shariff-button {
			margin: 0 1px !important;
		}
	}

	@include respond-to("medium") {
		.shariff-button {
			margin: 0 5px !important;
		}
	}
}
