/*
	Teasers used on the project

*/
.teaser {
	position: relative;
	display: block;
	white-space: normal;
	overflow: hidden;

	.teaser__media {
		display: block;
		.aux-pos-top {
			padding: 20px;
			margin: 0;
		}

		.teaser__media--play {
			display: block;
			position: absolute;
			width: 41px;
			height: 25px;
			top: 50%;
			left: 50%;
			margin: -13px 0 0 -20px;
			z-index: 1;
			background-color: white;
			background-color: rgba(255, 255, 255, 0.8);
			text-align: center;
			line-height: 26px;
			color: #3d4855;

			@extend .icons-play;
		}
	}

	.teaser__cover {
		left: 0; bottom:0;
		padding: 20px 20px;
		box-sizing: border-box;

		transition: background-color, color 600ms linear;

		/* @include respond-to("up-than-large") {

		}
 		*/

		@include respond-to("medium-and-up") {
			bottom: 20px;
			margin-left: 20px;
			width: 75%;
			width: calc(75% - 20px);
			position: absolute;
		}

		/* @include respond-to("large") {
			width: 75%;
			width: calc(75% - 20px);
		} */

		@include respond-to("medium") {
			width: 100%;
			bottom: 0;
			margin-left: 0;
		}
	}

	.teaser__aux-wrapper .teaser__cover {
		width: 50%;
		@include respond-to("large") {
			width: 75%;
			width: calc(75% - 30px);
		}

		@include respond-to("medium") {
			width: 100%;
			margin-left: 0;
		}
	}


	&.col-xl-12 {
		.teaser__cover {
			@include respond-to("up-than-large") {
				width: 50%;
				width: calc(50% - 20px);
			}
		}
	}

	.teaser__cover--full {
		margin: 0;
		bottom: 0;
		width: 100%;
	}

	.teaser__title-cover,
	.teaser__title {
		padding-left: 0;
		padding-right: 0;
	}

	.teaser__title-cover {
		color: $teasers-title-color;
	}

	.teaser__copy-cover {
		color: $teasers-text-color;
	}

	/* .teaser__copy-cover:last-child {
		margin-bottom: 0;
	}
 */
	.teaser__body {
		padding: 20px;
		padding-bottom: 33px;

		&:after {
			content:"";
			position: absolute;
			top:0; left:0; bottom:0; right:0;
			z-index: -1;
		}

		.teaser__title,
		.teaser__copy {
			color: $text-color;
		}
	}

	.decorator {
		display: inline-block;
		width: 35px; height: 33px;
		background-color: $teaser-decorator-bgc;
		color: #FFF;
		&:before {
			text-align: center;
			width: 35px; line-height: 33px;
			vertical-align: bottom;
		}
	}

	.teaser__detail {
		height: 33px;
	    line-height: 33px;
	    padding: 0 35px 0 20px;
	    font-size: 11px;
	    white-space: nowrap;
	    text-overflow: ellipsis;
	    overflow: hidden;
	}

	.teaser__author {
		background: #98a2ad;
		color: white;
		padding: 10px;
		padding-left: 50px;
		position: relative;
		margin-right: 5px;
		margin-bottom: 5px;

		img {
			position: absolute;
			top: 0; left: 0;
			height: 100%;
		}
	}

	&:hover,
	&:focus {
		text-decoration: none;

		.decorator {
			background-color: $teaser-decorator-bgc-hover;
		}
	}

	.blockquote {
		background-color: transparent;

		p {
			position: relative;
			margin-bottom: 12px;
			line-height: 1.2;

			.icons-quote-end {
				position: absolute;
			}
		}

		p:last-child {
			margin-bottom: 7px;
		}

		i {
			color: inherit;
		}
	}

	&.is-slider {
		margin-bottom: 40px;
	}

	&.teaser--frame {
		padding-top: 20px;
		padding-left: 20px;
		padding-right: 20px;
		padding-bottom: 20px;

		.teaser__media {
			margin: 0;
		}

		.teaser__body {
			padding-top: 0;
			padding-right: 0;
			padding-bottom: 0;

			@include respond-to("medium") {
				padding-left: 0;
				padding-top: 20px;
				padding-right: 20px;
				padding-bottom: 20px;
			}
		}
	}
}

a.teaser {
	&:hover,
	&:focus {
		.decorator {
			background-color: $teaser-decorator-bgc-hover;
		}
	}
}

.aux-pos-top {
	margin-bottom: 20px;
}

.aux-pos-bt {
	position: absolute;
	bottom:0;
	left: 0;
	width: 100%;

	.decorator {
		position: absolute;
		bottom:0; right:0;
	}

	.btn {
		float: right;
		margin: 10px;
	}
}

.event-detail {
	.icons-calendar-small {
		font-size: 20px;
		vertical-align: middle;
	}
}
// out of teaser scope to avoid qualify
.teaser__media {
	background-size: cover;
	height: 100%;
	background-color: $color-cover-grey;
	position: relative;
	overflow: hidden;
	img {
		display: none;
	}
}

a.teaser__media:hover {
	.decorator {
		background-color: $color-teaser-orange-hover;
	}
}

.teaser__media--stage {
	@include respond-to("medium") {
		background-image: none !important;
		img {
			display: block;
			height: 100%;
			position: absolute;
		}

		& + .teaser__aux-wrapper .teaser__cover {
			position: static;
		}
	}
}

.teaser__media--fixed {
	background-size: auto;
    background-repeat: no-repeat;
    background-color: transparent;
    background-position: 100% 70%;
    position: absolute;
    top: 0; right: 0;
    height: 100%;

   @include respond-to("large") {
   		background-position: bottom left;
   }

   .teaser__media-img {
   		@include respond-to("medium-and-up") {
   			display: block;
   			position: absolute;
   			top: 0;
   			right: 0;
   		}
   }
}

