// multiple teasers per line
.flexbox.flexwrap .teaser__wrapper {
	display: flex;
	flex-wrap: wrap;

	// break the line
	.col-lg-12 {
		@include respond-to("large") {
			//flex-flow: column nowrap */
			& + .teaser {
				margin-top: 4px;
			}
		}
	}

	.col-md-12 {
		@include respond-to("medium") {
			& + .teaser {
				margin-top: 4px;
				margin-bottom: 0px !important;
			}
		}
	}

	.col-sm-12 {
		@include respond-to("small") {
			&.teaser {
				margin-bottom: 4px;
			}

			&:last-child {
				margin-bottom: 0;
			}
		}
	}

	.teaser.col-xl-4 {
		@include respond-to("up-than-large") {
			&.teaser:nth-child(3n + 1) {
				margin-left: 0;
			}
		}
	}

	.col-lg-6 {
		@include respond-to("large") {
			&:nth-child(2n + 1) {
				margin-left: 0;
			}

			&:nth-child(3n + 0) {
				&.teaser,
				&.teaser + .teaser {
					margin-top: 4px;
				}


			}
		}
	}
}

