			margin-right: 0;
		} */
	}

	&[data-column="2"] {
		.owl-item + .owl-item .teaser{
			margin-left: 3px;
		}
		/* .owl-item > .teaser {
			margin-right: 3px;
		} */
		/* .owl-item:nth-child(n+2) > .teaser {
			margin-right: 0;
		} */
	}

	.teaser__body:first-child {
		height: 100%;
	}
}

.is-slider--full {
	.owl-controls {
		position: static;
	}

	.icons-slider-prev,
	.icons-slider-next {
		top: calc(50% - 30px + 16px);
		font-size: 32px;
	}

	.icons-slider-prev:before {
		content: "\E00C";
	}
	.icons-slider-next:before {
		content: "\E028";
	}
}

.is-slider--facts {
	@include respond-to("medium-and-up") {
		.owl-item:first-child .teaser__body {
			padding-left: 0;
		}
	}
}

.teaser--text {
	min-height: $size3;

	@include respond-to("medium") {
		height: auto;

	}
}

/**
* when possible, implement auto size and let flexible handle
**/
.flexbox.flexwrap .teaser--auto-size {
	height: auto;
	min-height: 0;
}

/*
.teaser--stage {
	@include respond-to("medium") {
		height: $size3;

		.teaser__cover {
			height: 100%;
		}
	}
}
*/
.teaser--mq-text-only {
	@include respond-to("medium") {
		height: auto;
		.teaser__media {
			background-image: none !important;
			height: auto;
		}

		.teaser__cover {
			position: relative;
		}

		.teaser__title-cover, .teaser__copy-cover {
			margin-bottom: 10px;
		}
	}
}

.teaser--mq-nobg {
	@include respond-to("medium") {
		.teaser__media {
			display: none;
		}

		.teaser__cover {
			background-color: transparent;
			//max-width: 75%;
			//max-width: calc(75% - 20px);
			padding-left: 0;
			color: $color-cover-white;
		}
	}
	@include respond-to("small") {
		.teaser__cover {
			max-width: 100%;
		}
	}
}


.t--color-1 { color: $color-cover-grey; }
.t--color-2 { color: $color-cover-blue !important; }
.t--color-3 { color: $color-cover-black; }
.t--color-4 { color: $color-cover-white !important; }
.t--color-5 { color: $color-teaser-orange !important; }

.product-link {
	display: block;
    float: right;
    position: relative;
    margin-top: 8px;
}

.is-slider + .product-link {
	margin-top: -30px;
	@include respond-to("medium") {
		margin-top: 0;
	}
}

.teaser {
	.icons-teaser-download-link {
		vertical-align: middle;
		padding-right: 10px;
	}
}
.teaser--stage-full {
	.teaser__media {
		height: $size2;

		@include respond-to("medium-and-up") {
			height: $size7;
		}
	}
}

.teaser--stage {
	.teaser__media {
		height: $size2;

		@include respond-to("medium-and-up") {
			height: $size6;
		}
	}

	.t--bg-1 & .teaser__copy-cover {
		color: $teaser-stage-text-color;
	}
}

.teaser--stage-text {
	height: $size2;

	&.teaser--stage-text-auto {
		height: auto;
		min-height: $size2;
	}

	.limit-text {
		max-height: $size2 - 60px;
		&.limit-text--small {
			max-height: $size2 - 80px;
			height: $size2 - 80px;
		}
	}

	.teaser__body:after,
	.teaser__body {
		background-color: $teaser-stage-text-bgc;
	}


	// Note: ".teaser--stage-text .teaser__title" isn't enough to apply this color.
	// tag: @css-specificity
	.teaser__body .teaser__title {
		color: $teaser-stage-title-color;
	}

	.teaser__body .teaser__copy {
		color: $teaser-stage-text-color;
	}


	@include respond-to("up-than-large") {
		height: $size6;
		.limit-text {
			max-height: $size6 - 80px;
			&.limit-text--small {
				max-height: $size6 - 120px;
				height: $size6 - 120px;
			}
		}
	}

	&.teaser--stage-text-auto {
		@include respond-to("up-than-large") {
			height: auto;
			min-height: $size6;
		}
	}
}

.teaser--stage-cio-view {
	img {
		display: none;
	}
	@include respond-to("up-than-large") {
		.limit-text.limit-text--small {
			max-height: 200px;
		}
		img {
			position: absolute;
			bottom: 0;
			right: 0;
			display: block;
		}
		.btn {
			position: absolute;
			bottom: 20px;
		}
	}


}

.teaser--media-overlay {
	.teaser__media {
		height: $size4;
		@include respond-to("medium") {
			display: none;
		}
	}
	.teaser__cover {
		height: $size4;
		.limit-text {
			max-height: 220px;
		}
		@include respond-to("medium-and-up") {
			height: $height-1x * 1.5;

			.limit-text {
				max-height: 90px;
				&.limit-text--small {
					max-height: 70px;
				}
			}
		}
		&.teaser__cover--full {
			@include respond-to("medium") {
				position: static;
			}
		}
	}
	.aux-pos-top {
		position: absolute;
		top: 0; left: 0;
		padding: 20px;
		z-index: 20;
		@include respond-to("medium") {
			& ~ .teaser__cover {
				padding-top: 60px;
				.limit-text {
					max-height: 180px;
				}
			}
		}
	}
	/* @include respond-to("medium") {
		height: $height-1x * 1.5;
	} */
}

.teaser--media {
	.teaser__media {
		height: $height-1x * 1.5;
	}

	.teaser__cover {
		height: $height-1x * 1.5;
		position: relative;
		.limit-text {
			max-height: 90px;
			&.limit-text--small {
				max-height: 70px;
			}
		}
	}

	&.teaser--media--taller {
		.teaser__media {
			height: $size1;
		}

		.teaser__cover {
			height: $size2;
			.limit-text {
				max-height: 130px;
			}
		}
		.teaser__body {
			height: $size4;
			box-sizing: border-box;
			.limit-text {
				max-height: 235px;
			}
		}
	}
}

.teaser--text {
	.limit-text {
		max-height: 180px;
	}
}

.teaser--facts {
	.text-light {
		color: $teaser-facts-color;
	}

	&:not(.t--bg-1):hover {
		.text-light,
		.h2,
		.text-light {
			color: $teaser-facts-color-hover;
		}
	}

	.blockquote p {
		margin-bottom: 20px;
	}
}

.teaser--thumb {
	height: 125px;

	&.is-active {
		opacity: 0.5;

		a {
			cursor: default;
			.btn.btn-video:hover {
				background-color: #e29317 !important;
			}
		}
	}
}
.teaser--small, .teaser--small .teaser__media {
	height: $height-1x * 1.5;
}

.teaser--thumb {
	.limit-text {
		max-height: 125px;
	}
}

.teaser--small, .teaser--small .teaser__media {
	@include respond-to("medium-and-up") {
		height: $size2;
	}

	.limit-text {
		max-height: 90px;
		@include respond-to("medium-and-up") {
			max-height: 130px;
		}
	}
}

.teaser--small {
	.teaser__body {
		width: calc(100% - 120px);
		@include respond-to("medium-and-up") {
			width: calc(100% - 160px);
		}
	}
	.teaser__media {
		width: 120px;
		@include respond-to("medium-and-up") {
			width: 160px;
		}
	}
}

.teaser--thumb {
	$teaser--thumb-width: 222px;

	.teaser__body {
		max-height: 125px;
		@include respond-to("up-than-large") {
			width: calc(100% - $teaser--thumb-width);
		}
	}

	.teaser__media {
		width: $teaser--thumb-width;
		max-width: 50%;
		@media (min-width: $break-medium + 1) and (max-width: $break-large) {
			width: auto;
			background-image: none !important;
			.aux-pos-bt { position: static; }
			.btn { margin-top: 45px; }
		}
		@include respond-to("small") {
			width: auto;
			background-image: none !important;
			.aux-pos-bt { position: static; }
			.btn { margin-top: 45px; }
		}
	}
}

.teaser--thumb .teaser__body { padding-top:0; padding-bottom:0; }



.teaser--media-half {
	@extend .teaser--media-block;
	height: $size3;

	.teaser__media {
		height: $size3;
		display: block;
		@include respond-to("small") {
			display: none;
		}
	}

	.teaser__body {
		height: 100%;
	}

	.limit-text {
		max-height: 180px;
	}
}

&.is-slider {
	.teaser--media-half {
		.teaser__media {
			//@include respond-to("medium-and-up") {
				width: 50%;
			//}
		}
	}
}

.teaser--tall {
	height: $size6;

	.limit-text {
		max-height: 300px;
	}
	@include respond-to("small") {
		height: $size4;
		.limit-text {
			max-height: 220px;
		}
	}

	.teaser__cover {
		height: $size3;
		position: absolute;
		.limit-text {
			max-height:180px;
		}
		@include respond-to("small") {
			@extend .teaser__cover--solid;
			height: $size4;
			position: relative;
			padding-top: 60px;
			.limit-text {
				max-height:220px;
			}
		}
	}

	.teaser__media {
		@include respond-to("small") {
			display: none;
		}
	}

	.teaser__media ~ .aux-pos-top {
		position: absolute;
		top: 0; left: 0;
		padding: 20px;
		z-index: 20;
		/* @include respond-to("medium") {
			& ~ .teaser__cover {
				padding-top: 60px;
				.limit-text {
					max-height: 180px;
				}
			}
		} */
	}
}

.teaser--expert {
	.teaser__body {
		padding-bottom: 26px;
	}
	.teaser__media {
		background-color: transparent;
	}
	.limit-text {
		@include respond-to("medium-and-up") {
			max-height: 85px;
		}
	}

	@include respond-to("medium") {
		.teaser__media-img {
			display: none;
		}
	}
}

.lt-ie9 .teaser--mq-text-only .teaser__cover {
	position: absolute;
}

.teaser--keep-cover {
	@include respond-to("medium") {
		/* .teaser__cover {
			position: absolute;
		} */
		height: auto;
		.teaser__media {
			height: $size2;
		}
	}
}


.teaser--video {
	@include respond-to("medium-and-up") {
		height: $size4;
	}
	@include respond-to("medium") {
		height: 100%;
	}
}

.teaser--nine-position {
	@include respond-to("medium-and-up") {
		height: 430px;
	}

	.teaser__body {
		padding-top:0;
	}

	.limit-text {
		height: 70px;
	}

	.social-bar,
	.social-bar__wrapper,
	.social-bar__wrapper.social-bar--teaser .social-bar {
		height: 33px;
	}
}

// to show teaser--frame inside internal-concept
.internal-concept {
	.teaser {
		&.teaser--frame {
			.teaser__body {
				padding-top: 0;
				@include respond-to("small") {
					padding-top: 20px;
				}
			}
		}
	}
}

.icons-success:before { display: none; }
.active .icons-success:before { display: inline-block; }

.teaser-and-video--slider {
	.owl-item {
		.teaser {
			margin-left: 3px;
		}

		&:first-child .teaser {
			margin-left: 0;
		}

	}
}

// Share snippet aligment
.teaser__container {
	position: relative;

	// Soci
	.aux-pos-bt {
		width: auto;
		bottom: 7px;
	}

	.social-bar__wrapper {
		width: 35px;
		&.active {
			width: auto;
		}
	}
}

// @note @theme
@if $components-version == 'onlinebanking' {
	.t--bg-1,
	.t--bg-2,
	.t--bg-3,
	.t--bg-4 {
		&.teaser--media-block  {
			.teaser__body {
				background-color: $teaser-experts-bgc;
			}

			.teaser__body:hover {
				background-color: $teaser-experts-bgc-hover;
			}

			.teaser__body .teaser__title {
				color: $teaser-experts-tite-color;
			}

			.teaser__body .teaser__copy {
				color: $teaser-experts-color;
			}

			&:hover .teaser__body {
				background-color: $bright-blue-2;
			}
		}

		&.teaser--nine-positions {
			.teaser__body .teaser__title {
				color: $teaser-nine-positions-color;
			}

			.teaser__media {
				background-color: $teaser-nine-positions-bgc;
			}
		}

		&.teaser--text {
			.teaser__title {
				color: $teaser-text-title-color;
			}

			.teaser__copy {
				color: $teaser-text-color;
			}

			.teaser__detail {
				color: $teaser-text-detail-color;
			}
		}

		&.teaser--tall {
			.teaser__title,
			.teaser__title-cover {
				color: $teaser-tall-title-color;
			}

			.teaser__copy {
				color: $teaser-tall-color;
			}

			.teaser__detail {
				color: $teaser-tall-detail-color;
			}
		}

		&.teaser--media,
		&.teaser--media-overlay,
		&.teaser--mq-nobg.teaser--mq-text-only,
		&.teaser--stage-full,
		&.teaser--keep-cover {
			.teaser__title-cover {
				color: $rangoon-green;
			}
			.teaser__copy-cover {
				color: $dark-gray;
			}
		}

		&.teaser--media-half {
			background-color: #F0EFEF;

			.teaser__body {
				background-color: #F0EFEF;
			}
			.teaser__body .teaser__title {
				color: $rangoon-green;
			}
			.teaser__copy {
				color: $dark-gray;
			}

			&:hover {
				.teaser__body {
					background-color: #F0EFEF;
				}
			}
		}

		&.teaser--media-block.teaser--frame {
			.teaser__copy {
				color: $dark-gray;
			}

			.teaser__body {
				background-color: #f0efef;
			}

			&:hover {
				.teaser__body {
					background-color: #f0efef;
				}
			}
		}
	}

	.lk-icon
	.lk-icon.white,
	.product-link {
		color: $dark-gray;
	}
}
