.fw-container {
	background-color: $content-color;
	&:hover {
		background-color: $content-color;
	}

	@include respond-to("large") {
		margin-top: 4px;
	}
}

.fw-box {
	position: relative;

	.absolute-el {
		top: 20px;
		left: 20px;
		padding: 6px;
		z-index: 1000;

		a {
			color: $base;
		}

		@include respond-to("small") {
			top: 10px;
			left: 10px;
		}
	}

	.bt-reset {
		box-sizing: initial;

		&.text-bt.gr-disable {
			border: none !important;
			background-color: #3d4855 !important;
			background-image: none !important;
		}

		&.text-bt.gr-active {
			border: none !important;
			background-color: $link-color !important;
		}

		&.text-bt {
			font-family: $basic-font !important;
			font-size: 11px !important;
		}
	}

	#period {
		top: 20px;
		right: 20px;
		@include respond-to("small") {
			left: 10px;
			right: auto;
			top: 72px;
		}
	}

	.overmap {
		background-image: none !important;
		/* background-color: $text-primary !important;
		background-color: rgba($text-primary, 0.8) !important; */
	}

	.fw-small.fwt-tool-c .gr-gray {
		background-color: $content-color;
		@include respond-to("small") {
			padding-top: 30px;
		}
	}
}

.fw-box-internal-concept {
	display: none;

	@include respond-to("large") {
		display: block;
	}

	.svg-container {
		height: 316px;
		border: 1px solid transparent;
	}
}

.fw-small.fwt-tool-c .text-bt {
	box-sizing: content-box;
}
.lt-ie9 .fw-small.fwt-tool-c .text-bt {
	height: 30px;
}

//ipad avoid orange border
.fw-small.fwt-tool-c {
	background-color: transparent !important;
}

.fw-regular.fwt-tool-c .pp-wrapper {
	box-sizing: content-box;
}
